	module.exports = {"data":{"postsJson":{"title":"Seven Bowls","questionsTitle":"HUNGERING TO KNOW GOD MORE","display":"block","bookName":"Revelation","ref":"15:1-16:21","html":"<p>   <strong>15</strong><u>1</u>I saw another great and marvelous sign in the sky: seven angels having the seven last plagues, for in them God’s wrath is finished. <u>2</u>I saw something like a sea of glass mixed with fire, and those who overcame the beast, his image,* and the number of his name, standing on the sea of glass, having harps of God. <u>3</u>They sang the song of Moses, the servant of God, and the song of the Lamb, saying,</p><h1>ALL THE REDEEMED</h1><h4>   Great and marvelous are your works, Lord God, the Almighty!<br />   Righteous and true are your ways, you King of the nations.<br />   <u>4</u>Who wouldn’t fear you, Lord,<br />   and glorify your name?<br />   For you only are holy.<br />   For all the nations will come and worship before you.<br />   For your righteous acts have been revealed.</h4><p>   <u>5</u>After these things I looked, and the temple of the tabernacle of the testimony in heaven was opened. <u>6</u>The seven angels who had the seven plagues came out, clothed with pure, bright linen, and wearing golden sashes around their breasts.<br />   <u>7</u>One of the four living creatures gave to the seven angels seven golden bowls full of the wrath of God, who lives forever and ever. <u>8</u>The temple was filled with smoke from the glory of God, and from his power. No one was able to enter into the temple, until the seven plagues of the seven angels would be finished.<br />   <strong>16</strong><u>1</u>I heard a loud voice out of the temple, saying to the seven angels,</p><h1>GOD</h1><h2>Go and pour out the seven bowls of the wrath of God on the earth!</h2><p>   <u>2</u>The first went, and poured out his bowl into the earth, and it became a harmful and evil sore on the people who had the mark of the beast, and who worshiped his image.<br />   <u>3</u>The second angel poured out his bowl into the sea, and it became blood as of a dead man. Every living thing in the sea died.<br />   <u>4</u>The third poured out his bowl into the rivers and springs of water, and they became blood. <u>5</u>I heard the angel of the waters saying,</p><h1>A THIRD ANGEL</h1><h4>You are righteous, who are and who were, you Holy One, because you have judged these things. <u>6</u>For they poured out the blood of the saints and the prophets, and you have given them blood to drink. They deserve this.</h4><p><u>7</u>I heard the altar saying,</p><h1>JESUS</h1><h2>Yes, Lord God, the Almighty, true and righteous are your judgments.</h2><p>   <u>8</u>The fourth poured out his bowl on the sun, and it was given to him to scorch men with fire. <u>9</u>People were scorched with great heat, and people blasphemed the name of God who has the power over these plagues. They didn’t repent and give him glory.<br />   <u>10</u>The fifth poured out his bowl on the throne of the beast, and his kingdom was darkened. They gnawed their tongues because of the pain, <u>11</u>and they blasphemed the God of heaven because of their pains and their sores. They didn’t repent of their works.<br />   <u>12</u>The sixth poured out his bowl on the great river, the Euphrates. Its water was dried up, that the way might be prepared for the kings that come from the sunrise. <u>13</u>I saw coming out of the mouth of the dragon, and out of the mouth of the beast, and out of the mouth of the false prophet, three unclean spirits, something like frogs; <u>14</u>for they are spirits of demons, performing signs; which go out to the kings of the whole inhabited earth, to gather them together for the war of that great day of God, the Almighty.</p><h1>JESUS</h1><h2><u>15</u>Behold, I come like a thief. Blessed is he who watches, and keeps his clothes, so that he doesn’t walk naked, and they see his shame.</h2><p><u>16</u>He gathered them together into the place which is called in Hebrew, Megiddo.<br />   <u>17</u>The seventh poured out his bowl into the air. A loud voice came out of the temple of heaven, from the throne, saying,</p><h1>GOD</h1><h2>It is done!</h2><p><u>18</u>There were lightnings, sounds, and thunders; and there was a great earthquake, such as was not since there were men on the earth, so great an earthquake, so mighty. <u>19</u>The great city was divided into three parts, and the cities of the nations fell. Babylon the great was remembered in the sight of God, to give to her the cup of the wine of the fierceness of his wrath. <u>20</u>Every island fled away, and the mountains were not found. <u>21</u>Great hailstones, about the weight of a talent,* came down out of the sky on people. People blasphemed God because of the plague of the hail, for this plague is exceedingly severe.</p><h5>*Revelation 15:2 TR adds “his mark,”<br />*Revelation 16:21 1 talent is about 34 kilograms or 75 pounds</h5>","path":"/429","Question1":"Take note of the acts of God described in this story.","Question2":"What do these reveal about the character of God?","Question3":"Have you experienced this dimension of the character of God in your life? If yes, how so? If not, how might you come to experience this?","Question4":"What can you do to grow in your desire to know and experience God more intimately today?","prevPath":"/428","nextPath":"/430"}},"pathContext":{}}

